<template>
  <div id="search">
    <div id="searchBar">
     <input type="text" @keydown.enter='sendSearch()' v-model="nowSearch" @blur="sendSearch()">
     <div><a href="#/main">取消</a></div>
    </div>
    <hr>
    <div id="history">
        <span>搜索记录</span>
        <ul>
        <li v-for="msg in history">{{msg}}</li>
        <div @click="clear()" v-if="this.history.length">清空搜索记录</div>
        </ul>
    </div>
  </div>
</template>

<script>
export default {
    data() {
        return {
            nowSearch:'',
            history:[]
        }
    },
    methods: {
        sendSearch(){
            if(this.nowSearch!='')
            this.history.push(this.nowSearch);
            this.nowSearch='';
            localStorage.setItem('history',JSON.stringify(this.history));
            //将histor中记录转换为json格式然后存入localstorage中
        },
        clear(){
            this.history.splice(0,this.history.length);
            localStorage.setItem('history','');
        }
    },
    mounted:function(){
        if(localStorage.getItem('history')!='')
        this.history=JSON.parse(localStorage.getItem('history'));
        //当网页挂载时将本地储存中的搜索记录传到history中然后渲染
    }
};
</script>
<style scoped>
#search{
    height: 100%;
}
#searchBar{
    /* border: red 1px solid; */
    height: 50px;
    display: flex;
    align-items: center;
}
#searchBar input{
    outline: none;
    border: none;
    height: 80%;
    background-color: #f4f4f4;
    color: #a0a0a0;
    border-radius: 5px;
    margin-left: 5%;
    flex: 8;
    font-size: 100%;
}
#searchBar div{
    font-size: 120%;
    display: inline-block;
    flex: 2;
    text-align: center;
}
#searchBar div a{
    color: #d4237a;
}
hr{
    color: #a0a0a0;
}
#history{
    color: #818181;
}
#history ul {
    margin: 40px 0 0 0;
}
#history ul div{
    color: #d4237a;
}
#history ul li{
    margin-top: 2vw;
    font-size: 6vw;
    padding-left:10vw;
    border-bottom:1px rgba(83, 82, 82, 0.767) solid;
}
</style>