import VueRouter from 'vue-router'
import VideoShouye from '../components/VideoShouye'
import DonghuaVue from "@/pages/Donghua.vue";
import FanjuVue from "@/pages/Fanju.vue";
import GuochuangVue from "@/pages/Guochuang.vue";
import KejiVue from "@/pages/Keji.vue";
import MeishiVue from "@/pages/Meishi.vue";
import ShenghuoVue from "@/pages/Shenghuo.vue";
import WudaoVue from "@/pages/Wudao.vue";
import YinyueVue from "@/pages/Yinyue.vue";
import YouxiVue from "@/pages/Youxi.vue";
import YundongVue from "@/pages/Yundong.vue";
import ZhishiVue from "@/pages/Zhishi.vue";
import Search from "@/components/Search.vue"
import Main from "../Mani.vue"

export default new VueRouter({
    routes: [
        {
            path: '/',
            redirect: '/search'
        },
        {
            path: '/main',
            redirect: '/shouye',
            components: {
                main: Main,
            },
            children: [
                {
                    name: 'VideoShouye',
                    path: '/shouye',
                    component: VideoShouye,
                },
                {
                    path: '/dongman',
                    component: DonghuaVue
                }
                ,
                {
                    path: '/fanju',
                    component: FanjuVue
                },
                {
                    path: '/guochuang',
                    component: GuochuangVue
                },
                {
                    path: '/yinyue',
                    component: YinyueVue
                },
                {
                    path: '/wudao',
                    component: WudaoVue
                },
                {
                    path: '/youxi',
                    component: YouxiVue
                },
                {
                    path: '/zhishi',
                    component: ZhishiVue
                },
                {
                    path: '/keji',
                    component: KejiVue
                },
                {
                    path: '/yundong',
                    component: YundongVue
                },
                {
                    path: '/shenghuo',
                    component: ShenghuoVue
                },
                {
                    path: '/meishi',
                    component: MeishiVue
                }
            ]
        },
        {
            path: '/search',
            components: { main: Search }
        }
    ]
})