<template>
  <div id="main">
    <div id="nav">
      <div id="zoneDetail">
        <li :class="select[0]" @click="movenote(0)">
          <router-link to="/shouye" active-class='selected'>首页</router-link>
        </li>
        <li :class="select[1]" @click="movenote(1)">
          <router-link to="/dongman" active-class='selected'>动画</router-link>
        </li>
        <li :class="select[2]" @click="movenote(2)">
          <router-link to="/fanju" active-class='selected'>番剧</router-link>
        </li>
        <li :class="select[3]" @click="movenote(3)">
          <router-link to="/guochuang" active-class='selected'>国创</router-link>
        </li>
        <li :class="select[4]" @click="movenote(4)">
          <router-link to="/yinyue" active-class='selected'>音乐</router-link>
        </li>
        <li :class="select[5]" @click="movenote(5)">
          <router-link to="/wudao" active-class='selected'>舞蹈</router-link>
        </li>
        <li :class="select[6]" @click="movenote(6)">
          <router-link to="/youxi" active-class='selected'>游戏</router-link>
        </li>
        <li :class="select[7]" @click="movenote(7)">
          <router-link to="/zhishi" active-class='selected'>知识</router-link>
        </li>
        <li :class="select[8]" @click="movenote(8)">
          <router-link to="/keji" active-class='selected'>科技</router-link>
        </li>
        <li :class="select[9]" @click="movenote(9)">
          <router-link to="/yundong" active-class='selected'>运动</router-link>
        </li>
        <li :class="select[10]" @click="movenote(10)">
          <router-link to="/shenghuo" active-class='selected'>生活</router-link>
        </li>
        <li :class="select[11]" @click="movenote(11)">
          <router-link to="/meishi" active-class='selected'>美食</router-link>
        </li>
        <div id="nowSelect" :style="`left:${noteleft}`"></div>
      </div>
      <div id="showAll"><img src="../assets/更多.png" alt="" /></div>
    </div>
    <router-view></router-view>
    <!-- <VideoShouye/> -->
  </div>
</template>

<script>
import VideoShouye from './VideoShouye'
export default {
  name: "ZoneNav",
  components:{VideoShouye},
  data() {
    return {
      select: [
        "selected",
        "noselected",
        "noselected",
        "noselected",
        "noselected",
        "noselected",
        "noselected",
        "noselected",
        "noselected",
        "noselected",
        "noselected",
        "noselected",
      ],
      noteleft: "1.9%",
    };
  },
  methods: {
    movenote(num) {
      for (var i = 0; i < 12; i++) this.select[i] = "noselected";
      this.select.splice(num, 1, "selected");
      if (num == 0) this.noteleft = "1.9%";
      if (num == 1) this.noteleft = "15%";
      if (num == 2) this.noteleft = "28%";
      if (num == 3) this.noteleft = "41%";
      if (num == 4) this.noteleft = "54%";
      if (num == 5) this.noteleft = "68%";
      if (num == 6) this.noteleft = "81%";
      if (num == 7) this.noteleft = "95%";
      if (num == 8) this.noteleft = "108%";
      if (num == 9) this.noteleft = "121%";
      if (num == 10) this.noteleft = "134.6%";
      if (num == 11) this.noteleft = "148%";
    },
  },
};
</script>

<style scoped>
#main{
    height: 100%;
}
#nav {
  height: 8%;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}
#nav #zoneDetail {
  width: 10%;
  font-size: 110%;
  flex: 9;
  overflow: auto;
  white-space: nowrap;
}
#nav #zoneDetail::-webkit-scrollbar{
  width: 0px;
}
#nav #zoneDetail #nowSelect {
  position: relative;
  left: 1.9%;
  height: 1vw;
  width: 10%;
  background-color: #d4237a;
  transition: 0.4s;
}
#nav #zoneDetail li {
  display: inline-block;
  color: #505050;
  margin: 3% 1.5%;
}
#nav #showAll {
  height: 50%;
  flex:1;
}
#nav #showAll img {
  height: 100%;
}
#nav #zoneDetail li .selected {
  color: #d4237a;
}
#nav #zoneDetail .noselected {
  color: #505050;
}
</style>>
